import React, { Component } from 'react';
import { NavLink } from "react-router-dom";

class Navbar extends Component {
	render() {
		return(
			<div className="navbar">
				<div className="navbar-brand">
					<NavLink to="/" activeClassName='is-active'>MLib</NavLink>
				</div>
				<div className="navbar-toggle">
					<button className="btn btn-hamburger float-right" data-toggle='toggle-menu'>
						<span></span>
					</button>
				</div>
				<div className="navbar-menu">
					<li><NavLink to="/" exact activeClassName='is-active' className="menu-link">Trang chủ</NavLink></li>
					<li><NavLink to="/docs" activeClassName='is-active' className="menu-link">Tài liệu</NavLink></li>
					<li><NavLink to="/examples" activeClassName='is-active' className="menu-link">Ví dụ</NavLink></li>
					{/*<li className="menu-drop">	
						<a className="menu-link" href="/#">Download</a>
						<div className="wrapper-menu-drop"> 
							<a className="menu-link" href="/#">Use CDN</a>
							<a className="menu-link" href="/#">Download v0.0.1</a>
						</div>
					</li>*/}
					<div className="search ml-auto">
						<form className="form-inline">
							<input type="text" className="form-input mr-1" placeholder="Search" />
							<button className="btn btn-outline-green">Search</button>
						</form>
					</div>
				</div>
			</div>
		);
	}
}
export default Navbar;