import React, { Component } from 'react';
import { NavLink } from "react-router-dom";
import brandLogo from'../brand.png';

class Home extends Component {
	componentDidMount(){
	    document.title = "Mlib - The framework HTML CSS, JS"
	}
	render() {
		return(
			<div className="container-home">
		   		<div className="ch-left">
		   			<div className="ch-left--intro">
		   				<h1 className="h1">MLib thư viện CSS</h1>
		   				<p className="h5 text-normal">Xây dựng 1 trang web nhanh chóng, tối ưu giao diện cho mọi thiết bị với MLib</p>
						<p className="h5 text-normal mb-2">
							Bạn có thể thiết kế trang web của bạn một cách nhanh chóng mà không cần phải CSS lại, thư viện chúng tôi còn có hỗ trợ tối ưu giao diện cho mọi thiết bị ví dụ điện thoại, máy tính bảng, PC.
						</p>
						<p>
							<NavLink to='/docs'><button className="btn btn-red no-radius mr-1" data-ripple='true'>Bắt đầu ngay</button></NavLink>
							<NavLink to='/docs/download'><button className="btn btn-dark no-radius" data-ripple='true'>Tải xuống</button></NavLink>
						</p>
		   			</div>
		   		</div>
		   		<div className="ch-right">
		   			<img src={brandLogo} alt="brand"/>
		   		</div>
		   </div>
		);
	}
}
export default Home;