import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import { Switch, Route } from "react-router-dom";
import CDN from "../Docs/CDN";
import Download from "../Docs/Download";
import Introduction from "../Docs/Introduction";
import StartedProject from "../Docs/StartedProject";

class MainLeft extends Component {
	render() {
		return(
			<div className="container-left">
				<Switch>
					<Route path="/docs/cdn" component={CDN} />
					<Route path="/docs/download" component={Download} />
					<Route path="/docs/started" component={StartedProject} />
					<Route path="/docs" component={Introduction} />
				</Switch>
			</div>
		);
	}
}



export default MainLeft;