import React, { Component } from 'react';

class Download extends Component {
	componentDidMount(){
	    document.title = "Download - Mlib"
	}
	render() {
		return(
			<div className='py-1'>
		  		<h1>Các lựa chọn tải xuống</h1>
		  		<p className='h5 text-normal'>Tải xuống các file cần thiết để xem các file CSS JS đã được biên dịch.</p>
		  		
		  		<h4 className='mt-2'>Tải xuống CSS và JS</h4>
		  		<div className='h5 text-normal'>
		  			Tải xuống mã đã biên dịch sẵn sàng để sử dụng cho MLib v0.0.1 để dễ dàng đưa vào dự án của bạn, bao gồm:
		  			<ul>
		  				<li>File biên dịch và minified của CSS</li>
		  				<li>File biên dịch và minified của JS</li>
		  			</ul>
		  			Các tài nguyên tải xuống trên không bao gồm tài nguyên bên ngoài như (JQuery)
		  		</div>
		  		<button className='btn btn-dark'>Tải xuống</button>
		  		
		  		<h4 className='mt-2'>Tải xuống mã nguồn CSS và JS</h4>
		  		<div className='h5 text-normal'>
		  			Tải xuống cấu trúc mã nguồn của MLib bao gồm
		  			<ul>
		  				<li>File sass biên dịch cho CSS</li>
		  			</ul>
		  			Các tài nguyên tải xuống trên không bao gồm tài nguyên bên ngoài như (JQuery)
		  		</div>
		  		<button className='btn btn-dark'>Tải xuống mã nguồn</button>
		  		
		  		<h4 className='mt-2'>Tải xuống các ví dụ</h4>
		  		<div className='h5 text-normal'>
		  			Nếu bạn muốn tải xuống và xem xét các ví dụ của chúng tôi, bạn có thể lấy các ví dụ đã được tạo sẵn:
		  		</div>
		  		<button className='btn btn-dark'>Tải xuống ví dụ</button>
			</div>
		);
	}
}
export default Download;