import React, { Component } from 'react';

class CDN extends Component {
	componentDidMount(){
	    document.title = "CDN - Mlib"
	}
	render() {
		return(
			<div className='py-1'>
		  		<h1>Tại sao phải sử dụng CDN?</h1>
		  		<p className='h5 text-normal'>Chỉ cần nhúng vào file của bạn là có thể sử dụng nhanh chóng, không cần thiết phải tải xuống rồi link vào project bạn mắc công lu bu.</p>
		  		
		  		<h4 className='mt-2'>CSS</h4>
		  		<p className='h5 text-normal'>Sao chép-dán biểu định kiểu &lt;link&gt; vào &lt;head&gt; của bạn trước tất cả các biểu định kiểu khác để tải CSS của chúng tôi.</p>
		  		<div className="highLightCode">
				   <div className='code-wrap'>
					   <div className="code-bracket">&lt;</div>
					   <div className="code-tag">link</div>
					   <div className="code-prop">&nbsp;rel=</div><div className="code-value">"stylesheet"</div>
					   <div className="code-prop">&nbsp;href=</div><div className="code-value">"https://mqp99.github.io/cdn/mlib/v0.0.1/css/mlib.min.css"</div>
					   <div className="code-bracket">/&gt;</div>
				   </div>
				</div> 

		  		<h4 className='mt-2'>JS</h4>
		  		<p className='h5 text-normal'>Nhiều thành phần của chúng tôi yêu cầu hàm Javascript để nó có thể hoạt động. Thư viện chúng tôi cần sử dụng Jquery để hoạt động vì vậy bạn thêm Jquery trước sau đó đến MLib. Sao chép-dán đoạn mã này vào dưới thẻ đóng &lt;/body&gt;</p>
		  		<div className="highLightCode">
				   <div className='code-wrap'>
					   <div className="code-bracket">&lt;</div>
					   <div className="code-tag">script</div>
					   <div className="code-prop">&nbsp;src=</div><div className="code-value">"https://ajax.googleapis.com/ajax/libs/jquery/3.5.1/jquery.min.js"</div>
					   <div className="code-bracket">&gt;</div>
					   <div className="code-bracket">&lt;/</div>
					   <div className="code-tag">script</div>
					   <div className="code-bracket">&gt;</div>
				   </div>
				   <div className='code-wrap'>
					   <div className="code-bracket">&lt;</div>
					   <div className="code-tag">script</div>
					   <div className="code-prop">&nbsp;src=</div><div className="code-value">"https://mqp99.github.io/cdn/mlib/v0.0.1/js/mlib.min.js"</div>
					   <div className="code-bracket">&gt;</div>
					   <div className="code-bracket">&lt;/</div>
					   <div className="code-tag">script</div>
					   <div className="code-bracket">&gt;</div>
				   </div>
				</div>
			</div>
		);
	}
}
export default CDN;