import React, { Component } from 'react';
import { NavLink } from "react-router-dom";

class MainRight extends Component {
	render() {
		return(
			<div className="container-right">
				<div className='menu-right'>
					<div className='menu-right-title'>Getting started</div>
					<ul className='menu-right-item'>
						<li><NavLink exact to="/docs" activeClassName='active'>Introduction</NavLink></li>
						<li><NavLink exact to="/docs/started" activeClassName='active'>Start Project</NavLink></li>
					</ul>
				</div>
				<div className='menu-right'>
					<div className='menu-right-title'>Installation</div>
					<ul className='menu-right-item'>
						<li><NavLink to="/docs/cdn" activeClassName='active'>CDN</NavLink></li>
						<li><NavLink to="/docs/download" activeClassName='active'>Download</NavLink></li>
					</ul>
				</div>
				<div className='menu-right'>
					<div className='menu-right-title'>Layout</div>
					<ul className='menu-right-item'>
						<li><a href="/#">Overview</a></li>
						<li><a href="/#">Grid</a></li>
						<li><a href="/#">Utilities for layout</a></li>
					</ul>
				</div>
				<div className='menu-right'>
					<div className='menu-right-title'>Component</div>
					<ul className='menu-right-item'>
						<li><a href="/#">Button</a></li>
						<li><a href="/#">Carousel</a></li>
						<li><a href="/#">Navbar</a></li>
						<li><a href="/#">Input</a></li>
						<li><a href="/#">Modal</a></li>
					</ul>
				</div>
				<div className='menu-right'>
					<div className='menu-right-title'>Utilities</div>
					<ul className='menu-right-item'>
						<li><a href="/#">Border</a></li>
						<li><a href="/#">Color</a></li>
						<li><a href="/#">Display</a></li>
						<li><a href="/#">Flex</a></li>
						<li><a href="/#">Float</a></li>
						<li><a href="/#">Spacing</a></li>
						<li><a href="/#">Text</a></li>
					</ul>
				</div>
			</div>
		);
	}
}

export default MainRight;